@font-face {
  font-family: "Exo 2";
  src: url("https://fonts.googleapis.com/css?family=Exo+2:300,400,700&display=swap"); }

.sectionContainer, .sectionContainerShiny {
  height: 100vh;
  width: calc(100vw - 100px);
  padding: 0 50px;
  font-family: "Exo 2", sans-serif;
  font-size: 24px;
  color: #eaeaea;
  font-weight: 300;
  overflow: hidden; }

.sectionContainerShiny {
  background-color: #cc3399;
  padding-top: 100px;
  height: auto; }

.sectionTitle {
  position: relative;
  margin-bottom: 30px;
  display: inline-block; }
  .sectionTitle::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 60%;
    border-top: 1px solid #eaeaea;
    -webkit-transition: width 350ms ease;
    transition: width 350ms ease; }
  .sectionTitle:hover::after {
    width: 0;
    right: 0;
    left: auto;
    border-top: 1px solid #00ebc7; }
