@font-face {
  font-family: "Exo 2";
  src: url("https://fonts.googleapis.com/css?family=Exo+2:300,400,700&display=swap"); }

h1 {
  font-size: 72px;
  color: #eaeaea;
  font-family: "Exo 2", sans-serif;
  width: 55%; }

input,
textarea {
  display: block;
  border-radius: 6px;
  border: solid 1px #979797;
  background-color: #2b2b2b;
  width: 200px;
  margin-bottom: 20px; }

input {
  padding: 10px; }

.flex {
  display: -webkit-box;
  display: flex; }

.social {
  display: -webkit-box;
  display: flex;
  -webkit-box-flex: 1;
          flex: 1;
  -webkit-box-pack: justify;
          justify-content: space-between; }
  .social svg {
    margin: 0 10px;
    font-size: 1.5em; }

a {
  color: #cc3399; }
  a svg {
    color: white;
    margin: 0 10px; }
  a:first-child {
    margin: 0; }

.grid {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
          justify-content: space-between; }

.spikes {
  position: relative;
  background: #2b2b2b;
  height: 50vh; }

.spikes::after {
  content: "";
  position: absolute;
  right: 0;
  left: -0%;
  top: 100%;
  z-index: 10;
  display: block;
  height: 175px;
  background-size: 175px 100%;
  background-image: linear-gradient(135deg, #2b2b2b 25%, transparent 25%), linear-gradient(225deg, #2b2b2b 25%, transparent 25%);
  background-position: 0 0; }

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 36px; }
  .flex {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
            flex-direction: column-reverse; }
  .social {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column; }
  .socialIcons {
    padding-top: 30px;
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: justify;
            justify-content: space-between; } }
