@font-face {
  font-family: "Exo 2";
  src: url("https://fonts.googleapis.com/css?family=Exo+2:300,400,700&display=swap"); }

nav {
  padding: 50px;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
          justify-content: space-between;
  position: relative; }
  nav .logo {
    -webkit-transition: -webkit-transform 350ms ease;
    transition: -webkit-transform 350ms ease;
    transition: transform 350ms ease;
    transition: transform 350ms ease, -webkit-transform 350ms ease; }
    nav .logo:hover {
      -webkit-transform: rotate(7deg);
              transform: rotate(7deg); }
  nav .linksContainer {
    display: -webkit-box;
    display: flex;
    color: red; }
    nav .linksContainer li {
      list-style: none;
      margin: 0 20px;
      font-family: "Exo 2", sans-serif;
      color: #dff5f1;
      position: relative; }
      nav .linksContainer li a,
      nav .linksContainer li a:visited {
        color: #dff5f1;
        text-decoration: none; }
      nav .linksContainer li::after {
        content: "";
        position: absolute;
        bottom: 20px;
        left: 0;
        width: 40%;
        border-top: 1px solid #eaeaea;
        -webkit-transition: width 350ms ease;
        transition: width 350ms ease; }
      nav .linksContainer li:hover::after {
        width: 0;
        right: 0;
        left: auto;
        border-top: 1px solid #00ebc7; }

.liquidContainer {
  width: 50px;
  height: 50px;
  position: relative; }
  .liquidContainer .circle {
    position: absolute;
    bottom: 9px;
    left: 5px;
    border-radius: 50%;
    overflow: hidden;
    height: 20px;
    width: 25px;
    border-radius: 50%;
    -webkit-transform: rotate(38deg);
            transform: rotate(38deg); }
    .liquidContainer .circle::after {
      content: "";
      position: absolute;
      border-radius: 10px 0.5em 20px 52px;
      width: 29px;
      height: 29px;
      opacity: 0.9;
      background: -webkit-gradient(linear, left top, left bottom, from(#36d8c5), to(#6319a0));
      background: linear-gradient(to bottom, #36d8c5 0%, #6319a0 100%);
      -webkit-animation: drift 3000ms infinite cubic-bezier(0.5, 0.7, 0.7, 0.5);
              animation: drift 3000ms infinite cubic-bezier(0.5, 0.7, 0.7, 0.5); }

.liquidTop {
  width: 27px;
  height: 10px;
  border-radius: 50%;
  position: absolute;
  bottom: 20px;
  left: 5px;
  z-index: 10;
  opacity: 1;
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(54, 216, 197, 0)), color-stop(40%, rgba(54, 216, 197, 0)), to(rgba(99, 25, 160, 0.8)));
  background: linear-gradient(to bottom, rgba(54, 216, 197, 0) 0%, rgba(54, 216, 197, 0) 40%, rgba(99, 25, 160, 0.8) 100%);
  animation: wobble 4s infinite alternate-reverse; }

@-webkit-keyframes drift {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  from {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg); } }

@keyframes drift {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  from {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg); } }
